body {
  margin: 0;
  padding: 0;
  font-family: Roboto,sans-serif !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* flex */
.flex { display: flex; }
.horizontal { flex-direction: row; }
.vertical { flex-direction: column; }
@media screen and (max-width: 720px){
  .horizontal { flex-direction: column; }
}
.flex-center, .flex-start, .flex-end { align-items: center; }
.flex-center { justify-content: center; }
.flex-start { justify-content: flex-start; }
.flex-end { justify-content: flex-end; }
.wrap { flex-wrap: wrap; }

.flex-1 { flex: 1; }
.flex-2 { flex: 2; }
.flex-3 { flex: 3; }
.flex-4 { flex: 4; }
.flex-5 { flex: 5; }
.flex-6 { flex: 6; }
.flex-7 { flex: 7; }
.flex-8 { flex: 8; }
.flex-9 { flex: 9; }

.container {
  width: calc(100% - 20px);
  padding: 0 10px;
  max-width: 1100px;
  margin: 0 auto;
}

/* componentes */
.TopBar {
  background-color: #561208;
  color: #FFFFFF;
  padding: 8px 0;
  font-size: 0.7rem;
}
.TopBar i, .TopBar strong { color: #B27D01; }
.TopBar i { padding: 0 5px; font-size: 0.9rem; }
.TopBar .item-topo { 
  padding: 0 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.Header {
  padding: 20px 0;
}
.Header .logo {
  max-width: 170px;
}
.Header .input-pesquisa {
  width: 90%;
  border: 1px solid #aaaa;
  border-radius: 10px 0 0 10px;
  padding: 10px 15px;
  font-size: 1rem;
}
.Header .button-pesquisa {
  background-color: #561208;
  color: #B27D01;
  font-size: 1.1rem;
  padding: 10px 15px;
  border: 0;
  border-radius: 0 10px 10px 0;
  cursor: pointer;
}
.Header .cart {
  font-size: 1.78rem;
  color: #561208;
  cursor: pointer;
}
.cart-number {
  color: #561208;
  background-color: #B27D01;
  font-weight: bold;
  padding: 3px 5px;
  width: 13px;
  border-radius: 100px;
  margin-left: -10px;
  margin-top: -25px;
}

.Categorias {
  background-color: #561208;
  
  color: #FFF;
  font-weight: bold;
  border-bottom: 4px solid #B27D01;
}
.categoria {
  padding: 8px 5px;
  font-size: 0.9rem;
  cursor: pointer;
}
.categoria i {
  padding: 0 5px;
}
.categoria span {
  text-align: center;
}
.categoria-all, .categoria:hover {
  background-color: #B27D01;
}

.mini-pic-container {
  cursor: pointer;
}
.breadcrumbs {
  padding: 20px 0;
  font-size: 0.85rem;
  color: rgb(90, 90, 90);
}
.titulo {
  font-size: 1.2rem;
  color: #561208;
}
.titulo h2 { margin: 0; }
.titulo span {
  font-size: 0.9rem;
  color: #444;
}
.preco {
  margin: 25px 0;
}
.preco h3 {
  color: #B27D01;
  font-size: 2rem;
  margin: 0 -1px;
  margin-left: -1px;
}
.preco h3 small {
  color: #777;
  font-size: 0.85rem;
}
.preco .full-value {
  font-weight: bold;
  color: #888;
  text-decoration: line-through;
}
.preco .full-value {
  font-weight: bold;
  color: #444;
  margin: 8px 0 3px;
}
.preco .part-value {
  color: #777;
  font-size: 0.9rem;
}
.preco .part-value span {
  color: #555;
  font-weight: bold;
}
.opcao {
  padding: 10px 5px;
}
.opcao span {
  font-size: 0.8rem;
  color: #444;
}
.opcao select { width: 120px; }
.opcao select, .opcao input {
  padding: 5px 10px;
  font-size: 0.8rem;
  margin: 2px 0;
  border-radius: 5px;
  border: 1px solid #aaa;
}
.btn-cta {
  width: 100%;
  background-color: #561208;
  color: #FFFFFF;
  padding: 10px 15px;
  margin: 30px 0 0;
  font-size: 1.3rem;
  font-weight: bold;
  border: 0;
  cursor: pointer;
}
.btn-cta:hover {
  background-color: #2c0903;
}
.hr {
  margin: 30px 0;
  height: 2px;
  background-color: #561208;
}
.aviso-frete {
  border-left: #B27D01 5px solid;
  padding: 15px 20px;
  margin: 15px 0;
  color: rgb(95, 67, 2);
  background-color: rgb(255, 241, 208);
  font-size: 0.9rem;
}
.input-cep {
  padding: 10px 15px;
  font-size: 0.9rem;
  border-radius: 10px 0 0 10px;
  border: 1px solid #aaaa;
  width: 100px;
}
.button-cep {
  background-color: #555;
  color: #FEFEFE;
  font-weight: bold;
  border: 0;
  border-radius: 0 10px 10px 0;
  font-size: 0.9rem;
  padding: 10px 15px;
}

.Descricao h2 {
  color: #561208;
  margin: 50px 0 30px;
}
table.descricao {
  border-collapse: collapse;
}
table.descricao td:first-child {
  background-color: #561208;
  color: #FEFEFE;
  font-weight: bold;
  padding: 10px 15px;
}
table.descricao td:last-child {
  background-color: #B27D01;
  color: #FEFEFE;
  padding: 10px 15px;
  border-bottom: 1px solid #561208;
}

.Depoimentos {
  margin: 60px 0;
}
.Depoimentos h2 {
  color: #561208;
  margin-bottom: 50px;
}
.depoimento {
  padding: 20px;
  border: 10px;
  box-shadow: 0 0 5px #ccc;
  margin: 0 10px;
  border-radius: 10px;
}
.depoimento span {
  height: 60px;
}

.ProdutosRelacionados,
.ProdutosVisitados {
  margin: 20px 0 70px;
}
.ProdutosRelacionados h2,
.ProdutosVisitados h2 {
  color: #561208;
  margin-bottom: 40px;
}
.prod-image {
  padding: 10px;
}
.prod-titulo {
  font-size: 0.9rem;
  padding: 0 10px;
  height: 100px;
}
.prod-preco h4 {
  text-align: center;
  font-size: 1.5rem;
  color: #561208;
  font-weight: bold;
  margin: 5px 0 2px;
}
.prod-preco span {
  text-align: center;
  font-size: 0.9rem;
  color: #666;
} 
.item-produtos {
  max-width: 25%;
}

.Institucional {
  background-color: #561208;
  color: #FFF;
  padding: 20px 0;
}
.Institucional h2 {
  border-bottom: 1px solid #B27D01;
  margin-right: 10px;
  padding-bottom: 5px;
}
.Institucional span {
  font-size: 0.9rem;
  padding: 2px 0;
  cursor: pointer;
} 
.Institucional span:hover {
  text-decoration: underline;
}
.Institucional i {
  font-size: 1.2rem;
  padding: 0 5px;
}
.Institucional .inti {
  padding: 0 10px;
}
.item-phones {
  background-color: #B27D01;
  border-radius: 10px;
  padding: 8px 12px;
}
.item-ph {
  padding: 10px 0;
}
.redes-sociais i {
  font-size: 2rem;
  margin-top: -10px;
  margin-bottom: 30px;
  cursor: pointer;
}
.redes-sociais i:hover {
  color: #B27D01;
}

.Footer {
  background-color: rgb(0, 0, 0);
  color: #FEFEFE;
  padding: 8px 0;
}

.mini-pic-container { 
  max-width: 100px;
  margin: 8px;
}

.no-mb { display: block; }
.no-mb-flex { display: flex; }
.only-mb-flex { display: none; }
.preco h3 small { display: flex; }
@media screen and (max-width: 720px){
  .no-mb, .no-mb-flex { display: none; }
  .only-mb-flex { display: flex; }
  .logo-header { margin: 5px 0; }
  .categoria { min-width: 120px; }
  .overflow-x { overflow-x: scroll; }
  .breadcrumbs { font-size: 0.8rem; } 

  .depoimento { margin: 10px; }
  .item-produtos { max-width: 50%; flex-basis: 50% !important; margin-bottom: 15px; }
  .prod-titulo { height: 130px; }
  .titulo { margin-top: 15px; }
  .preco h3 small { display: block; }
}